.shape-one, .shape-two {
    position: absolute;
    left: 0px;
    top: 0px;
}
.shape-two {
    bottom: 0px;
    top: auto;
}
.shape-three, .shape-four {
    right: 2px;
    left: auto;
}
.shape-one:before, .shape-two:before {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    width: 10px;
    height: 1px;
    background-color: #000;
}
.shape-one:after, .shape-two:after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    width: 1px;
    height: 10px;
    background-color: #000;
}
.shape-two:before, .shape-two:after {
    top: auto;
    bottom: 0px;
}
.shape-one:before, .shape-two:before {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    width: 10px;
    height: 1px;
    background-color: #000;
}
.shape-three:before, .shape-four:before, .shape-three:after, .shape-four:after {
    left: auto;
    right: 0px;
}
.prt_btn:hover .shape-one:after, .prt_btn:hover .shape-two:after, .prt_btn:hover .shape-one:before, .prt_btn:hover .shape-two:before {
  background:#ecbc33;
}
